export default {
  data() {
    return {
			// 预览图片的地址
			previewList: [],
			// textarea 框内输入的内容
			val: '',
			// 上传图片的地址
			imageSrc: [],
		};
  },
  methods: {
		// 点击上传图片
		upload() {
			uni.chooseImage({
				count: 9, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: res => {
					this.previewList.push(res.tempFilePaths[0])
					this.imageSrc = res.tempFilePaths[0]
					// for (var i = 0; i < res.tempFilePaths.length; i++) {
					// 	this.previewList.push(res.tempFilePaths[i])
					// 	this.imageSrc.push(res.tempFilePaths[i])
					// }
					uni.uploadFile({
						url: 'http://192.168.1.143:8086/WNC/user/addOpinion',
						filePath: this.imageSrc,
						name: 'file',
						formData: {
							userId: 1,
							// msg: this.val
						},
						success: res => {
							console.log(res)
						}
					})
				}
			})
		},
		// 点击预览图片
    preview(index) {
      uni.previewImage({
        urls: [this.previewList[index]]
      })
		},
	// 意见反馈接口
		submit() {
			console.log((this.imageSrc))
			// console.log(JSON.stringify(this.imageSrc))
			if (this.val !== '') {
				// uni.uploadFile({
				// 	url: 'http://192.168.1.143:8086/WNC/user/addOpinion',
				// 	filePath: JSON.stringify(this.imageSrc),
				// 	name: 'file',
				// 	formData: {
				// 		userId: 1,
				// 		msg: this.val
				// 	},
				// 	success: res => {
				// 		console.log(res)
				// 	}
				// })
				uni.request({
					url: "http://192.168.1.143:8086/WNC/user/addOpinion",
					data: {
						userId: 1,
						msg: this.val
					},
					header: {
						"Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
					},
					success: res => {
						console.log(res);
					}
				});
			} else {
				uni.showToast({
						title: '请输入文字内容',
						duration: 2000,
						icon: "none"
				});
			}
			// uni.request({
			// 	url: "http://192.168.1.143:8086/WNC/user/addOpinion",
			// 	data: {
			// 		userId: 1,
			// 		msg: '1232'
			// 	},
			// 	header: {
			// 		"Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
			// 	},
			// 	success: res => {
			// 		console.log(res);
			// 	}
			// });
		}
	}
};
